import * as React from 'react';
import { connect } from 'react-redux';
import { InputGroup, FormControl, Button } from 'react-bootstrap';
import './SignUp.css';
import * as yup from 'yup';
import { UserReducerType_SignIn, StatusReducerType_Idle , GameReducerType_NumberMemory, GameReducerType_ReactionTime, GameReducerType_VerbalMemory, GameReducerType_VisualMemory, GameReducerType_FraudDetection, GameReducerType_Locked } from '../constants';


const gameTypeMap = {
    [GameReducerType_NumberMemory]: 'Number Memory Game',
    [GameReducerType_ReactionTime]: 'Reaction Time Game',
    [GameReducerType_VerbalMemory]: 'Verbal Memory Game',
    [GameReducerType_VisualMemory]: 'Visual Memory Game',
    [GameReducerType_FraudDetection]: 'Fraud Detection Game'
}

const isInteger = (message) => ({
    test(value) {
      return value == null || Number.isInteger(value);
    },
    message,
  });

const SignUp = ({game, setUserData }) => {
    const [data, setData] = React.useState({ name: '', mobile: '', userName: '', domain: '', college: 'IITM' });
    const [validationFailed, setValidationFailed] = React.useState(null);

    const handleChange = ({ target }) => {
        setData({ ...data, [target.id]: target.value });
    };

    const handleSave = () => {
        const schema = yup.object().shape({
            name: yup.string().required('Name is a required field.'),
            mobile: yup.number('Enter only numbers.').transform((mobile) => (isNaN(mobile) ? undefined : mobile)).test(isInteger('Enter only numbers.')),
            college: yup.string().required('Institution is a required field.'),
            email: yup.string().email('Please enter a valid email id.')
        });

        const formData = {name: data.name, mobile: data.mobile, email: data.userName && data.domain && `${data.userName}@${data.domain}`, college: data.college };

        schema.validate(formData).then(() => {
            setUserData(formData);
        }).catch(err => {
            setValidationFailed({ field: err.path, message: err.message });
        });
    };

    return (
        <div className="flex signUpForm flex-wrap flex-col bg-orange pad-30" style={{margin: 'auto'}}>
            <div className="TextTitle flex justify-center">{gameTypeMap[game]}</div>
            <div className="formTitle align-center justify-center flex" style={{marginTop: 0}}>Participant Signup Form</div>
            <div className="fieldSet">
                <label htmlFor="name" className="fieldTitle">Name:</label>
                <div className="fieldInput">
                    <FormControl size="lg" className={`${validationFailed && validationFailed.field === 'name' ? 'validationError' : ''}`}  id="name" aria-label="Name" aria-describedby="Participant Name" value={data.name} onChange={handleChange} />
                    {validationFailed && validationFailed.field === 'name' && <div className="validationMessage">{validationFailed.message}</div>}
                </div>
            </div>
            <div className="fieldSet">
                <label htmlFor="userName" className="fieldTitle">Email Address:</label>
                <div className="fieldInput">
                    <InputGroup size="lg" className={`${validationFailed && validationFailed.field === 'email' ? 'validationError' : ''}`}>
                        <FormControl id="userName" aria-label="User Name" aria-describedby="Email User Name" value={data.userName} onChange={handleChange} />
                        <InputGroup.Prepend>
                            <InputGroup.Text id="inputGroup-sizing-lg">@</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl id="domain" className="email-domain" aria-label="Domain" aria-describedby="Email Domain" value={data.domain} onChange={handleChange} />
                    </InputGroup>
                    {validationFailed && validationFailed.field === 'email' && <div className="validationMessage">{validationFailed.message}</div>}
                </div>
            </div>
            <div className="fieldSet">
                <label htmlFor="mobile" className="fieldTitle">Mobile Number:</label>
                <div className="fieldInput">
                    <InputGroup size="lg" className={`${validationFailed && validationFailed.field === 'mobile' ? 'validationError' : ''}`}>
                        <InputGroup.Prepend>
                            <InputGroup.Text id="inputGroup-sizing-lg">+91</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl id="mobile" aria-label="Mobile" aria-describedby="Mobile Number" value={data.mobile} onChange={handleChange} />
                    </InputGroup>
                    {validationFailed && validationFailed.field === 'mobile' && <div className="validationMessage">{validationFailed.message}</div>}
                </div>
            </div>
            <div className="fieldSet">
                <label htmlFor="college" className="fieldTitle">Institution:</label>
                <div className="fieldInput">
                    <FormControl size="lg" id="college" aria-label="Institution" aria-describedby="Institution" value={data.college} onChange={handleChange} />
                    {validationFailed && validationFailed.field === 'college' && <div className="validationMessage">{validationFailed.message}</div>}
                </div>
            </div>
            <div className="hintText">To be part of leadership board, please provide either a valid mobile number or email id.<br />This will enable us to contact you. Your email id or mobile number will not be used for any other purpose.</div>
            <div className="flex align-center justify-center mar-top-30">
                <Button variant="primary" onClick={handleSave}>Save</Button>
            </div>
        </div>
    );
};

const mapStateToProps = ({ game }) => ({
    game
});

const mapDispatchToProps = dispatch => {
    return {
        setUserData: (data) => {
            dispatch({ type: StatusReducerType_Idle});
            dispatch({ type: UserReducerType_SignIn, data });
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(SignUp);